@import url(https://fonts.googleapis.com/css2?family=Bahiana&display=swap);
body {
  margin: 0;
  font-family: "Bahiana", cursive;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.number {
  border: none;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  font-size: 20vw;
}
.name {
  font-size: 5vw;
}

.marg {
  padding-top: 15vh;
  height: 100vh;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.bar {
  position: fixed;
  top: 0;
  width: 100%;
  overflow: hidden;
  height: 48px;
  padding: 4px;
  padding-left: 20px;
  text-align: justify;
  display: flex;
  font-size: 40px;
  color: white;
  opacity: 0.7;
  text-decoration: none;
  background-color: black;
  color: beige;
}
.bar .baritem {
  margin-left: auto;
  margin-right: auto;
}

